.header {
  padding-top: 20px;
  min-height: 79px;

  &__container {
    @include flex-all-sb;
  }

  &__nav {
    margin-left: 200px;
  }

  &__btns {
    @include flex-all-sb;


    &-btn {
      background-color: var(--gray-color);
      border: none;
      border-radius: 7px;
      padding: 10px 18px 11px;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: var(--light-color);
      cursor: pointer;

      &:not(:last-child) {
        margin-right: 20px;
      }
    }

    &--cart {
      display: flex;


      svg {
        margin-right: 8px;
        width: 17px;
        height: 17px;
        fill: var(--light-color);
      }
    }

    &--text {
      margin-right: 8px;
    }

    &--number {
      @include flex-all-center;

      width: 20px;
      height: 18px;
      border-radius: 100%;
      background-color: var(--light-color);
      color: var(--gray-color);
      font-size: 11px;
    }
  }
}
