.about {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #e5e5e5;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    align-items: center;
    position: relative;
  }

  &__left {
    grid-column: 6 span;
    position: relative;
    z-index: 2;
  }

  &__right {
    grid-column: 6 span;
    padding-left: 104px;
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: var(--tree-color);
  }

  &__descr {
    max-width: 505px;
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: var(--gray-color);
  }

  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: var(--gray-color);

    &-img {
      background-image: url("../img/arrow.svg");
      background-position: right;
      background-repeat: no-repeat;
      padding-right: 30px;
    }
  }

  &-1 {
    position: absolute;
    width: 256px;
    height: 256px;
    top: -129px;
    left: 50%;
    transform: translateX(-50%);
  }

  &-2 {
    position: absolute;
    width: 256px;
    height: 256px;
    top: 77%;
    left: -111px;
  }
}
