.nav {

  &__list {
    @include flex;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 28px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    color: var(--tundora-color);
    transition: 0.3s ease-in-out;

    &:hover {
      color: var(--tree-color);
    }
  }
}
