.offering {
  padding-bottom: 100px;

  &__wrapper {
    width: 538px;

    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--tree-color);
    }

    &-title {
      margin: 0;
      margin-bottom: 7px;
      font-weight: 400;
      font-size: 36px;
      line-height: 43px;
      color: var(--gray-color);
    }

    &-descr {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--gray-color);
    }
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    padding-top: 20px;
    padding-bottom: 20px;
  }

  &__item {

    &-descr {
      margin: 0;
      margin-top: 18px;
      margin-bottom: 12px;
    }

    &-block {
      @include flex-all-sb;

      &--date {
        font-weight: 400;
        font-size: 13px;
        line-height: 18px;
        color: #6f7278;
      }
    }

    &-flexblock {
      @include flex-v-center;

      &--name {
        margin-left: 12px;
        font-weight: 400;
        font-size: 13px;
        line-height: 18px;
        color: var(--gray-color);
      }

      span {
        display: block;
        font-weight: 400;
        font-size: 11px;
        line-height: 15px;
        color: var(--tree-color);
      }
    }
  }

  &__guide {
    display: block;
    text-align: center;
  }

  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: var(--gray-color);

    &-img {
     background-image: url("../img/arrow.svg");
     background-position: right;
     background-repeat: no-repeat;
     padding-right: 30px;
    }
  }
}
