.shop {
  position: relative;
  top: -159px;
  width: 100%;
  min-height: 393px;
  background-color: var(--stromboli-color);
  background-image: url("../img/shop-bg.png");

  &__wrapper {
    display: block;
    margin: 0 auto;
    width: 538px;
    text-align: center;
    padding-top: 99px;

    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: #fafafc;
    }

    &-title {
      margin: 0;
      margin-bottom: 7px;
      font-weight: 400;
      font-size: 36px;
      line-height: 43px;
      color: var(--tree-color);
    }

    &-descr {
      margin: 0;
      margin-bottom: 21px;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #fafafc;
    }
  }

  &__btn {
    padding: 10px 18px 11px 18px;
    border: none;
    border-radius: 6px;
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    text-align: center;
    background-color: var(--light-color);
    color: var(--gray-color);
    transition: 0.3s ease-in-out;
    cursor: pointer;

    &:hover {
      background-color: var(--gray-color);
      color: var(--tree-color);
    }
  }
}

