.category {
  display: block;
  text-align: center;

  &__wrapper {
    margin-bottom: 21px;

    &-text {
      margin: 0;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--tree-color);
    }
  }

 &__list {
   display: grid;
   grid-template-columns: repeat(3, 379px);
   grid-template-rows: repeat(2, 473px);
   gap: 16px;
   padding-bottom: 20px;
 }

 &__image {
   border-radius: 10px;
   width: 379px;
   height: 473px;
 }

 &__item {
   position: relative;
 }

 &__link {
   font-weight: 400;
   font-size: 15px;
   line-height: 21px;
   color: var(--gray-color);

   &-img {
    background-image: url("../img/arrow.svg");
    background-position: right;
    background-repeat: no-repeat;
    padding-right: 30px;
   }
 }

 &__block {
   position: absolute;
   display: block;
   text-align: center;
   left: 0;
   top: 39px;
   width: 100%;

   &-title {
    text-align: center;
    margin: 0;
    margin-bottom: 10px;
    font-weight: 400;
    font-family: 'Roboto';
    font-size: 28px;
    line-height: 34px;
    color: var(--gray-color);
   }

   &-descr {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: var(--gray-color);
   }

   &-link {
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    color: var(--gray-color);

    &--img {
      background-image: url("../img/arrow.svg");
      background-position: right;
      background-repeat: no-repeat;
      padding-right: 30px;
    }
   }
 }
}

.info {
  position: relative;
  margin-top: 100px;
  height: 600px;
  background-image: url("../img/category-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__block {
    display: flex;
    align-items: center;
    justify-content: space-around;
    position: absolute;
    right: 40px;
    bottom: 40px;
    width: 615px;
    height: 48px;
    background: #292C32;
    border-radius: 16px;

    &-item {
      display: flex;

      &--first {
        padding-left: 8px;
        font-weight: 400;
        font-size: 11px;
        line-height: 12px;
        color: var(--light-color);

        span {
          padding-left: 5px;
        }
      }

      &--icon {
        width: 16px;
        height: 17px;
        fill: var(--light-color);
      }
    }
  }
}
