.setting {
  position: relative;
  z-index: 5;
  background-color: var(--light-color);

  &-1 {
    position: absolute;
    width: 470px;
    height: 423px;
    top: 0;
    left: 77%;
  }

  &-2 {
    position: absolute;
    width: 480px;
    height: 435px;
    bottom: 0;
    right: 75%;
  }

  &__wrapper {
    display: block;
    margin: 0 auto;
    width: 645px;
    text-align: center;

    &-text {
      margin: 0;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 2px;
      text-transform: uppercase;
      color: var(--tree-color);
    }

    &-descr {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: var(--gray-color);
    }
  }

  &__inner {
    margin-top: 71px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 110px;
    position: relative;
    z-index: 4;
  }

  &__item {
    width: 287px;
  }

  &__round {
    @include flex-all-center;

    margin: 0 auto;
    margin-bottom: 10px;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    background-color: var(--gray-color);
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    text-align: center;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    color: var(--gray-color);
  }
}
