.footer {
  padding-top: 100px;
  min-height: 567px;
  background-color: var(--gray-color);
  position: relative;
  z-index: 10;

  &__top {

    &-image {
      display: block;
      margin: 0 auto;
    }
  }

  &__middle {
    display: grid;
    gap: 70px;
    grid-template-columns: repeat(4, 1fr);
    padding-top: 75px;
    padding-bottom: 30px;
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: var(--tree-color);
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: var(--light-color);
    transition: 0.3s ease-in-out;

    &:hover {
      color: var(--tree-color);
    }
  }

  &__text {
    padding-bottom: 10px;
    margin: 0;
    width: 460px;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #fafafc;
  }

  &-form {
    position: relative;

    &__input {
      display: block;
      width: 458px;
      height: 53px;
      border-radius: 8px;
      padding-left: 21px;
      border: none;
      outline: none;
    }

    &__btn {
      position: absolute;
      top: 7px;
      right: 7px;
      bottom: 7px;
      padding: 10px 11px 9px 12px;
      background-color: var(--gray-color);
      border: none;
      border-radius: 10px;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: var(--light-color);
      transition: 0.3s ease-in-out;

      &:hover {
        color: var(--tree-color)
      }
    }
  }

  &__bottom {
    @include flex-all-sb;
  }
}

.copyright {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: var(--light-color);
}

.social {
  @include flex-v-center;

  &-item {
    &:not(:last-child) {
      margin-right: 20px;
    }
  }

  &-link {
    display: block;
    width: 24px;
    height: 24px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    &-in {
      background-image: url("../img/in.png");
    }

    &-fb {
      background-image: url("../img/fb.png");
    }

    &-insta {
      background-image: url("../img/insta.png");
    }

    &-tw {
      background-image: url("../img/tw.png");
    }
  }
}


